<template>
  <div class="shi-retake container">
    <div class="row">
      <form class="col s12" @submit="addToRetakeList">
        <div class="row">
          <div class="input-field col s12">
            <!-- This validate class is original validate entry -->
            <input v-model="smId" :class="isValidSmId()" id="smId" type="text">
            <label for="smId" data-error="SM编号格式不正确" data-success="SM编号格式正确">请输入NICO上PV的SM编号</label>
          </div>
        </div>
        <div class="row">
          <div class="col s12">
            <button class="btn waves-effect waves-light" type="submit">添加
              <i class="material-icons right">library_add</i>
            </button>
            <button class="btn waves-effect waves-light" type="reset">重置</button>
          </div>
        </div>
      </form>
    </div>
    <div class="row">
      <div class="col s12">
        <table  class="responsive-table">
          <thead>
            <tr>
              <th data-field="sm">编号</th>
              <!-- <th data-field="title">标题</th> -->
              <th data-field="status">状态</th>
              <th data-field="progress">进度</th>
              <th data-field="handle">操作</th>
              <th data-field="other">其他</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="retakeItem in retakeList">
              <td>{{ retakeItem.smId }}</td>
              <!-- <td>{{ retakeItem.title | showDataPlaceholder }}</td> -->
              <td>
                <span>{{ retakeItem.status | showStatusName }}</span>
              </td>
              <td>
                <div v-if="isShowProgress(retakeItem.status, 'circular')" class="preloader-wrapper small active">
                  <div class="spinner-layer spinner-green-only">
                    <div class="circle-clipper left">
                      <div class="circle"></div>
                    </div><div class="gap-patch">
                      <div class="circle"></div>
                    </div><div class="circle-clipper right">
                      <div class="circle"></div>
                    </div>
                  </div>
                </div>
                <div v-if="isShowProgress(retakeItem.status, 'indeterminate')" class="progress">
                    <div class="indeterminate"></div>
                </div>
                <div v-if="isShowProgress(retakeItem.status, 'doneIcon')">
                  <i class="material-icons">done</i>
                </div>
                <div v-if="isShowProgress(retakeItem.status, 'errorIcon')">
                  <i class="material-icons">error</i>
                </div>
              </td>
              <td>
                <button @click="reAppend($index)" v-if="isShowHandle(retakeItem.status, 'reAppend')" class="waves-effect waves-light btn">重新添加</button>
                <button @click="reProcess($index)" v-if="isShowHandle(retakeItem.status, 'reProcess')" class="waves-effect waves-light btn">重新处理</button>
              </td>
              <td>
                <a @click="removeAtRetakeList($index)" href="javascript:;" v-if="isShowOther(retakeItem.status, 'removeAtRetakeList')" ><i class="material-icons">delete</i></a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
  const $ = window.$
  const Materialize = window.Materialize
  const RETAKE_API = 'http://221.208.194.89?cmd=retake&id='
  const PROCLIST_API = 'http://221.208.194.89?cmd=proclist&id='
  export default {
    data () {
      return {
        smId: '',
        retakeList: []
      }
    },
    methods: {
      addToRetakeList (event) {
        event.preventDefault()
        const isValidFlag = this.isValidSmId()
        if (isValidFlag !== 'valid') return false
        const smId = this.smId
        if (_inRetakeList(this.retakeList, smId)) {
          this.smId = ''
          return false
        }
        const temp = {
          smId,
          title: '',
          status: 'append'
        }
        this.retakeList.push(temp)
        this._retaker(temp)
        this.smId = ''
        return false
      },
      removeAtRetakeList (index) {
        this.retakeList.splice(index, 1)
      },
      reAppend (index) {
        this.retakeList[index].status = 'append'
        this._retaker(this.retakeList[index])
      },
      reProcess (index) {
        this.retakeList[index].status = 'process'
        this._retaker(this.retakeList[index])
      },
      isValidSmId () {
        if (!this.smId) return ''
        if (!this.smId.match(/^[a-z]{2}\d*$/i)) return 'invalid'
        return 'valid'
      },
      isShowProgress (status, type) {
        if (status === 'append' && type === 'circular') {
          return true
        }
        if (status === 'process' && type === 'indeterminate') {
          return true
        }
        if (status === 'finished' && type === 'doneIcon') {
          return true
        }
        if (status.match(/Error$/i) && type === 'errorIcon') {
          return true
        }
      },
      isShowHandle (status, type) {
        if (!status.match(/Error$/i)) {
          return false
        }
        if (status === 'appendError' && type === 'reAppend') {
          return true
        }
        if (status === 'processError' && type === 'reProcess') {
          return true
        }
      },
      isShowOther (status, type) {
        if (status.match(/finished|Error$/i) && type === 'removeAtRetakeList') {
          return true
        }
      },
      _retaker
    },
    filters: {
      showStatusName (val) {
        let tempName
        switch (val) {
          case 'append':
            tempName = '添加中'
            break
          case 'process':
            tempName = '处理中'
            break
          case 'finished':
            tempName = '已完成'
            break
          case 'appendError':
            tempName = '添加出错'
            break
          case 'processError':
            tempName = '处理出错'
            break
        }
        return tempName
      },
      showDataPlaceholder (val) {
        if (!val) {
          return '－－'
        } else {
          return val
        }
      }
    },
    ready () {
    }
  }
  function _retaker (retakeItem) {
    // const self = this

    if (retakeItem.status === 'append') {
      $.getJSON(RETAKE_API + retakeItem.smId, function (data) {
        if (data.STATUS !== '[I]RequestAdded') {
          retakeItem.status = 'appendError'
          Materialize.toast(_showRequestAddedError(data.STATUS), 1000)
        } else {
          retakeItem.status = 'process'
          let timer = setInterval(function () {
            $.getJSON(PROCLIST_API + retakeItem.smId, function (data) {
              if (!data[0]) {
                retakeItem.status = 'finished'
                clearInterval(timer)
                return true
              }
              if (data[0].STATUS === '1') {
                return false
              }
              retakeItem.status = 'processError'
              clearInterval(timer)
            })
          }, 5000)
        }
      })
      return true
    }

    // if (retakeItem.status === 'append') {
    //   setTimeout(function () {
    //     retakeItem.status = 'appendError'
    //   }, 3000)
    //   return true
    // }

    // if (retakeItem.status === 'append') {
    //   setTimeout(function () {
    //     retakeItem.status = 'process'
    //     retakeItem.title = '【初音ミクV4X】 海色 【カバー/オリジナルMV】'
    //     setTimeout(function () {
    //       retakeItem.status = 'processError'
    //     }, 3000)
    //   }, 3000)
    //   return true
    // }

    // if (retakeItem.status === 'process') {
    //   setTimeout(function () {
    //     retakeItem.status = 'processError'
    //   }, 3000)
    // }

    // if (retakeItem.status === 'process') {
    //   setTimeout(function () {
    //     const index = _inRetakeList(self.retakeList, retakeItem.smId)
    //     self.retakeList.splice(index, 1)
    //     Materialize.toast('完成:' + retakeItem.title, 3000)
    //   }, 3000)
    // }
  }
  function _inRetakeList (retakeList, smId) {
    for (let key in retakeList) {
      const retakeItem = retakeList[key]
      if (retakeItem.smId === smId) {
        return {
          key
        }
      }
    }
    return false
  }
  function _showRequestAddedError (code) {
    return code
  }
</script>



